import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/smapiot/pidoc/tree/develop/packages/example-app/docs/generators/03-react.md";
    const html = `
<h1 id="react-generator" tabindex="-1">React Generator</h1>
<p>The Markdown generator transforms single JSX files with a <code>default</code> export (called the page content component) into documentation pages.</p>
<p>Importantly, for the meta information you need to use a comment like shown below:</p>
<pre><code class="hljs language-jsx"><span class="hljs-keyword">import</span> * <span class="hljs-keyword">as</span> <span class="hljs-title class_">React</span> <span class="hljs-keyword">from</span> <span class="hljs-string">&#x27;react&#x27;</span>;

<span class="hljs-comment">/**
title: Example
description: ...
section: Tools
**/</span>

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> <span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) {
  <span class="hljs-keyword">return</span> (
    <span class="language-xml"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      Hello World!
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span></span>
  );
}
</code></pre>
<p>Let's say you have a folder structure like this:</p>
<pre><code class="hljs language-plain">docs/
docs/foo/content.jsx
docs.config.json
</code></pre>
<p>A sitemap section in <em>docs.config.json</em> to cover this would be:</p>
<pre><code class="hljs language-json"><span class="hljs-punctuation">{</span>
  <span class="hljs-comment">// ...</span>
  <span class="hljs-attr">&quot;sitemap&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
    <span class="hljs-attr">&quot;example&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
      <span class="hljs-attr">&quot;title&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Example&quot;</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;sections&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">[</span>
        <span class="hljs-punctuation">{</span>
          <span class="hljs-attr">&quot;generator&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;react&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;segment&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;dir&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;foo&quot;</span>
        <span class="hljs-punctuation">}</span>
      <span class="hljs-punctuation">]</span>
    <span class="hljs-punctuation">}</span>
  <span class="hljs-punctuation">}</span>
<span class="hljs-punctuation">}</span>
</code></pre>
<p>This will look up the <em>.jsx</em> and <em>.tsx</em> files in the <code>foo</code> directory below the <code>docs</code> documentation root directory.</p>
`;
    const meta = {"title":"React","description":"Information about the React generator.","audience":"Everyone","level":"Beginner","section":"Generators"};
  

export default () => (
  <ContentPage>
    
    <PageLayout name={"default"} meta={meta}>
      <PageContent meta={meta}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    </PageLayout>
  
  </ContentPage>
);